@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);

.about {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 15px;
  align-items: stretch;
}

.about-left img {
  padding: 2%;
  width: 96%;
  border-radius: 5%;
  flex: 1 1;
}

.about-right {
  flex: 1 1;
  margin: 20px
}

.about-right h2 {
  font-family: "Barrio";
  font-weight: bold;
  font-size: 2em;
  color: rgb(77 114 138);
  border-bottom: 5px solid rgb(77 114 138);
  
}

.about-right p {
  font-size: 1em;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  .about {
flex-direction: row;
  }
  .about-left {
     width: 50%
  }
  .about-right {
    margin-left: 10px;
  }
}
.adopt {
  margin-top: 20px;
}

.adopt h2 {
  font-family: "Serif";
  font-weight: bold;
  font-size: 2.5em;
  text-align: center;
  color:  #4d728a;
}

.adopt h3 {
  font-size: 1.5em;
  font-weight: bold;
  font-family: "Serif";
  color:  #4d728a;
}

.button {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.left-button {
  margin-left: 0 auto;
  padding-right: 10px;
  flex : 1 1;
}

.right-button {
  margin-right: 0 auto;
  padding-left: 10px;
  flex :1 1;
}

.button-image {
  width: 100%;
}

@media screen and (min-width: 800px) {
  .button{
    flex-direction: row;
  }
  .left-button {
width: 49%;
  }

  .right-button {
    width: 49%;
  }


}
